@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

* {
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  overflow-x: hidden; 
} 

body {
  background: #111;
}

section {
  overflow: hidden;
  padding: relative;
  width: 100%;
  height: 100vh;
  min-height: 500px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

section img{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

section h2 {
  position: relative;
  color: #fff;
  font-size: 12em;
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

section .robokassa{
  position: fixed;
  color: #fff;
  top: 310px;
  left: 50%;
  width: 450px;
  margin-left: -225px;
  padding-bottom: 2rem;
  z-index: 10;
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

section .info{
  position: fixed;
  color: #fff;
  top: 20px;
  left: 50%;
  width: 450px;
  margin-left: -225px;
  font-size: 1.2rem;
  line-height: 1.8;
  text-align: justify;
  padding: 1rem;
  background-color: rgba(75, 0, 130, .6);
  border-radius: 6px;
}

section .contact{
  position: fixed;
  color: #fff;
  top: 260px;
  left: 50%;
  width: 450px;
  margin-left: -225px;
  padding-top: 10px;
  padding-bottom: 10px;
  a{
    font-size: 1.2rem;
    font-weight: bold;
    color: greenyellow;
  }
}

@media (min-width: 320px) and (max-width: 480px){
  section h2 {
    display: none;
    font-size:4.2rem;
  }
}

@media (min-width: 481px) and (max-width: 767px){
  section h2 {
    display: none;
    font-size:5.2rem;
  }
}

@media (min-width: 768px) and (max-width: 1200px){
  section h2 {
    display: none;
    font-size:10.2rem;
  }
}